
@value colors: '../assets/value.css';
@value third from colors;
.list{padding:0 .3rem;margin-bottom:1rem;
}
.list>li{line-height: .9rem;border-bottom:1px solid third;font-size: .28rem;
}
.list label{display: inline-block;width: 1.6rem;padding-left:.1rem;
}
.list input{width: 2rem; line-height:.9rem;font-size: .32rem;outline: none; -webkit-appearance: none;border: none;
}



