
@value colors: '../assets/value.css';
@value primary,third from colors;
.box{background: primary;  height: 12rem;
}
.in{width:6.3rem;height:.9rem;display: block;margin:1.2rem auto ; font-size: .32rem;  border-radius: 0.2rem;outline: none; -webkit-appearance: none;border: none;text-align: center;
}
.img{width:7rem;height: 5.5rem;margin: 0 auto;  display: block;
}
.btn{width: 2.7rem;line-height:.8rem;margin:0 auto;display:block;color:#fff;background: #63d5bb;font-size: .32rem;  text-align: center;border-radius: .2rem;
}



