
@value colors: '../assets/value.css';
@value third from colors;
.list{padding:0 .1rem;margin-bottom:1rem;font-size: 0;
}
.list>li{display: inline-block;margin-left:.05rem;
}
.list>.nomargin{margin-left: 0;
}
.list .txt{font-size: .3rem;padding:.15rem 0 .3rem;color: third;text-align: center;
}
.up{line-height:2.4rem;border:1px solid third;width: 2.4rem;font-size: .8rem;box-sizing: border-box;  text-align: center;display: block;border-radius: .2rem;
}



