
@value colors: '../assets/value.css';
@value second,third from colors;
.list{padding:.3rem;padding-bottom: 0;
}
.list>li{border:1px solid third;font-size: .26rem;margin-bottom: .1rem;position: relative; border-radius: .2rem;
}
.list div{display: inline-block;text-align: center;vertical-align: middle;line-height: 1.8;box-sizing: border-box;color: second
}
.list .tit{width: 2.4rem;line-height: 1rem;border-top-left-radius: .2rem;border-bottom-left-radius: .2rem;color: #fff;font-size: .32rem;
}
.status{width: 1.6rem;
}
.date{width:2.7rem;
}
.list h4{font-size: .26rem;
}
.status:after {content: ''; position: absolute;height: .6rem;width: 1px;border-left: 1px solid third;left:4.1rem;top: 50%;-webkit-transform: translateY(-50%);transform: translateY(-50%);
}
.done .tit{background: #8ece6c;
}
.done .st{color: #8ece6c;
}
.doing .tit{background: #f4b85e;
}
.doing .st{color: #f4b85e;
}
.desc{padding:0 .3rem;margin-bottom:.4rem;
}
.desc>li{border-bottom:1px solid third;font-size: .28rem;
}
.desc label{display: inline-block;width:2.6rem;padding-left:.1rem;  line-height: .9rem;
}
.desc b{width: 2rem; line-height:.9rem;font-size: .32rem;  float: right; text-align: right;padding-right:.2rem;
}
.desc p{padding: 0 .3rem 0.3rem;color: second;
}
.btn {margin-bottom:1rem;
}



