
body,ul,form{margin:0;padding:0;
}
dl,dd,h1,h2,h3,h4,h5,p,hr{margin:0;
}
body{font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;line-height:1;font-size:14px;color:#333;background-color:#fff;
}
h1,h2,h3,h4,h5{font-weight:400
}
table{border-collapse:collapse;border-spacing:0
}
img,hr{border:0
}
li{list-style:none
}
input,textarea{font-family:inherit;font-size:inherit;font-style:inherit;font-weight:inherit;outline:0;color:#333;
}
a{text-decoration:none;
}
:focus{outline:0;
}
em,i{font-style:normal
}
b{font-weight:normal
}
a{color:#333
}
em{color:#D41E23
}
::-webkit-input-placeholder{color:#ccc
}
h1{font-size:18px
}
h2{font-size:17px
}
h3,h4{font-size:16px
}
h5{font-size:14px;
}
:global .tc{text-align:center
}
:global .nowrap{max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;
}
:global .dn{display:none;
}
:global .l{float:left;
}
:global .r{float:right;
}
:global .pn{pointer-events:none;
}
:global .cf:after{content:'';display:table;clear:both;
}
:global #app{max-width:7.5rem;margin:0 auto;
}




