
@value colors: '../assets/value.css';
@value third from colors;
.hd{font-size: .32rem;padding:.6rem 0 .5rem;text-align: center;
}
.bottom{margin:.5rem .3rem;line-height: .9rem;border-bottom:1px solid third;font-size: .28rem;
}
